<template>
  <div class="nav-bar">
      <img src="../assets/img/icon/logo_ARE_header.png" class="nav-logo" />
    <nav class="nav-items">
      <div class="nav-box-item"><router-link :to="`/?token=${this.$route.query.token}`" class="nav-item">TICKETS</router-link></div>
      <div class="nav-box-item"><router-link :to="`/about-us?token=${this.$route.query.token}`" class="nav-item">NOSOTROS</router-link></div>
      <div class="nav-box-item"><router-link :to="`/artists?token=${this.$route.query.token}`" class="nav-item" href="#approach">ARTISTAS</router-link></div>
    </nav>
    <!-- <button class="hamburger-btn"><img src="../assets/img/icon/menu.svg" /></button> -->
    <label class="hamburger-menu">
      <input type="checkbox" ref="menuCheckbox" @click="toggleMenu">
    </label>
  </div>
  <aside class="sidebar" :class="{open: isMenuOpen}" ref="sidebar">
    
      <router-link to="/" class="nav-item z-10" @click.native="closeMenu">TICKETS</router-link>
      <router-link to="/about-us" class="nav-item z-10" @click.native="closeMenu" href="#approach">NOSOTROS</router-link>
      <router-link to="/artists" class="nav-item z-10" @click.native="closeMenu">ARTISTAS</router-link>
    </aside>
</template>

<script>

export default {
  name: "NavBar",
  data() {
    return {
      isScrolled: false,
      isMenuOpen: false,
    };
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
    document.addEventListener('click', this.handleClickOutside);
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll);
    document.removeEventListener('click', this.handleClickOutside);
  },
  
  methods: {
    handleScroll() {
      this.isScrolled = window.scrollY > 0;
    },
    toggleMenu() {
        this.isMenuOpen = !this.isMenuOpen
        console.log(this.isMenuOpen)
    },
    handleClickOutside(event) {
      if (this.isMenuOpen && !this.$refs.sidebar.contains(event.target) && !event.target.closest('.hamburger-menu')) {
        this.isMenuOpen = false;
        this.$refs.menuCheckbox.checked = false;
        console.log("yes")
      }
    },
    closeMenu() {
      this.isMenuOpen = false;
      if (this.$refs.menuCheckbox) {
        this.$refs.menuCheckbox.checked = false;
      }
      document.removeEventListener('click', this.handleClickOutside);
    },
  }
}
</script>

